:root {
  --hover-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  padding: 0 50px;
}

.header-content {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav {
  position: relative;
}

.nav__items {
  text-transform: uppercase;
  height: 60px;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  justify-content: center;
  align-items: center;
  height: 60px;
  margin-right: 20px;
  list-style-type: none;
  display: flex;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item-link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
}

.is-active {
  color: var(--hover-color);
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__item:hover .nav__item-link {
  color: var(--hover-color);
}

